    background-color: rgba(0, 0, 0, 0.822);
    border-radius: 10px;
    box-shadow: inset 5px 5px 7px rgba(255, 255, 255, 0.445), inset -5px -5px 7px rgba(255, 255, 255, 0.452);
    padding: 10px;
  }
/* main  */
  #mainDiv{
    display: block;
    width: 100vw;
  }
  #mainImage{
